import * as React from 'react';
import { Route, Routes } from 'react-router-dom';

// import './index.scss';

const CategoryUpdateView = React.lazy(() => import('./views.Update'));
const CategoryListView = React.lazy(() => import('./views.List'));

export default function CategoryRoutes() {
  return (
    <Routes>
      <Route path=":catSlug/" element={<CategoryUpdateView />} />
      <Route index element={<CategoryListView />} />
    </Routes>
  );
}

